.header{
  padding-top: 33px;
  position: relative;
  padding-bottom: 99px;
  padding-left: 20px;
  padding-right: 20px;
  @media (max-width: 400px){
    position: static;
    padding-left: 0;
    padding-right: 0;
  }
}
.header__container{
  max-width: 1320px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  position: relative;
  @media (max-width: 650px){
    justify-content: start;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
 }
 @media (max-width: 400px){
   flex-direction: row;
   align-items: center;
   width: 100%;
   margin-right: auto;
   margin-left: 0;
   justify-content: space-between;
  position: relative;
 }
  }

.header__list{
  display: flex;
  padding-left: 150px;
  @media (max-width: 786px){
    padding-left: 0;
  }
  @media (max-width: 650px){
    padding-top: 30px;
    padding-bottom: 30px;
  }
  @media (max-width: 400px){
    display: none;
  }
}
.header__logo{
  font-family: Gilroy;
font-style: normal;
font-weight: 600;
font-size: 30px;
line-height: 36px;
/* identical to box height */
z-index: 2;

color: #000000;
@media (max-width: 400px){
  margin: 0 auto;
}
}
.header__list__item{
  padding-left: 35px;
  @media (max-width: 400px){
    padding-top: 16px;
  }
}
.header__list__item:first-child{
  padding-left: 0;
  @media (max-width: 400px){
    padding-left: 35px;
    padding-top: 24px;
  }
}
.header__list__item__link{
  color: #000000;
  font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 23px;
/* identical to box height */


color: #000000;

}
.call-to-action{
  font-family: Circular;
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 23px;
/* identical to box height */


color: #FFFFFF;

width: 220px;
height: 71px;
z-index: 2;
@media (max-width: 400px){
  display: none;
}
}
.call-to-action-img{
  width: 220px;
  height: 71px;
}

.header__bac__left{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  padding-bottom: 99px;
 @media (max-width: 1300px){

   width: 300px;
   height: 600px;
 }
 @media (max-width: 400px){
 max-width: 50%;
  height: 500px;
  top: 240px;

}
}
.header__bac__right{
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1; 
  @media (max-width: 1300px){
    width: 300px;
    height: 600px;
    
  }
  @media (max-width: 400px){
    max-width: 50%;
  height: 400px;
  top: 327px;
  right: 0px;
  }
}
